body{
		color:#61656a;
}
.container{
	display: -ms-inline-flexbox;
	display: inline-flex;
	margin: 0px

}

.flip > .front{
	position: absolute;
	-webkit-transform:perspective(600px) rotateY(0deg);
	transform:perspective(600px) rotateY(0deg);
	background:white;
	width: 300px;
	height: 410px;

	-webkit-transition: webkit-transform .5s linear 0s;

	-o-transition: webkit-transform .5s linear 0s;

	transition: webkit-transform .5s linear 0s;
	-webkit-transition: -webkit-transform .5s linear 0s;
	transition: -webkit-transform .5s linear 0s;
	-o-transition: transform .5s linear 0s;
	transition: transform .5s linear 0s;
	transition: transform .5s linear 0s, -webkit-transform .5s linear 0s;
	border-radius: 2px;
	-webkit-box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
	        box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
	-webkit-transition: all .1s ease-in;
    -webkit-transform: scale(1);
    -ms-transition: all .1s ease-in;
    -ms-transform: scale(1);
    -moz-transition: all .1s ease-in;
    -moz-transform: scale(1);
    -o-transition: all .1s ease-in;
    transition: all .1s ease-in;
    transform: scale(1);
}
.card{
	width: 300px;
	height: 410px;
	background: 'white';
	-webkit-box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
	        box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 6px, rgba(0, 0, 0, 0.12) 0px 1px 4px;
	-webkit-transition: all .5s ease;
	-o-transition: all .5s ease;
	transition: all .5s ease;
	margin: 0 15px;
}
.image{
	background-image: url('https://static.pexels.com/photos/24249/pexels-photo-24249.jpg');
    background-size: cover;
	width: 100%;
	height: 40%;
}

.datos{
	margin-top: -20px;
	margin-left: 20px;
	text-align: left;
	color:#61656a;
}
.datos_back{
	margin-top: -30px;
	margin-left: 65px;
	text-align: left;
	color:#61656a;
}
.name{
	font-size: 12px;
}
project{
	font-size: 18px;
}
.description{
		color:#61656a;
		font-size: 12px;
		padding: 10px;
		width: 100%;
		overflow: hidden;
}
.meta{
	color:#61656a;
	font-size: 13px;
	text-align: left;
	margin-left: 25px;
}
.extras{
	color:white;
	font-size: 9px;
	text-align: left;
	left: 223px;
	position: absolute;
	top:10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: center;
	    align-items: center;
	background:-webkit-gradient(linear, left top, right top, from(#005fa8), color-stop(99%, #89ecb4), to(#89ecb4));
	background:-webkit-linear-gradient(left, #005fa8 0%, #89ecb4 99%, #89ecb4 100%);
	background:-o-linear-gradient(left, #005fa8 0%, #89ecb4 99%, #89ecb4 100%);
	background:linear-gradient(to right, #005fa8 0%, #89ecb4 99%, #89ecb4 100%);
	width: 73px;
	height: 15px;
	border-radius: 4px;
}

.iconos{
	margin-top: 5px;
}

.card:hover{
	-webkit-box-shadow: rgba(0, 0, 0, 0.20) 3px 8px 20px, rgba(0, 0, 0, 0.20) 3px 8px 16px;
	        box-shadow: rgba(0, 0, 0, 0.20) 3px 8px 20px, rgba(0, 0, 0, 0.20) 3px 8px 16px;
	cursor: pointer;
}
.inf_project{
	width: 100%;
	display: -ms-inline-flexbox;
	display: inline-flex;
}
.icon_dest{
	margin:0px;

}
.data_project .cantidad{
