@import url('https://fonts.googleapis.com/css?family=Open+Sans');


body{
	font-family: 'Open Sans', sans-serif;

}
.btn_uno{
	background:-webkit-gradient(linear, left top, right top, from(#b623a0), to(#d42677));
	background:-webkit-linear-gradient(left, #b623a0 0%, #d42677 100%);
	background:-o-linear-gradient(left, #b623a0 0%, #d42677 100%);
	background:linear-gradient(to right, #b623a0 0%, #d42677 100%);
	width:125px;
	border:transparent;
	border-radius: 4px;
	color: white;
	font-size: 15px;
	height:33px;
}
.fondo{
	width: 100%;
	height: 100vh;
}
.text{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	padding-top:15%;
	color:white;
}
.back{
	background-image: url('https://static.pexels.com/photos/196644/pexels-photo-196644.jpeg');
	background-position: center;
	background-position: fixed;
    background-repeat: no-repeat;
    width: 100%;
    height: 100vh;

}
#bg-video{
	position: fixed;
	top:0;
	left:0;
	margin: 0;
	min-width: 100%;
	min-height: 100%;


	z-index: -1000;
}

.degradado{

	height:100%;
	background: rgba(250,242,250,0.04);
	background: -webkit-gradient(left top, left bottom, color-stop(0%, rgba(250,242,250,0.04)), color-stop(3%, rgba(250,242,250,0.05)), color-stop(61%, rgba(28,28,28,0.3)), color-stop(84%, rgba(43,43,43,0.39)), color-stop(98%, rgba(20,19,20,0.45)), color-stop(100%, rgba(76,76,76,0.46)));
	background: -webkit-linear-gradient(top, rgba(250,242,250,0.04) 0%, rgba(250,242,250,0.05) 3%, rgba(28,28,28,0.3) 61%, rgba(43,43,43,0.39) 84%, rgba(20,19,20,0.45) 98%, rgba(76,76,76,0.46) 100%);
	background: -o-linear-gradient(top, rgba(250,242,250,0.04) 0%, rgba(250,242,250,0.05) 3%, rgba(28,28,28,0.3) 61%, rgba(43,43,43,0.39) 84%, rgba(20,19,20,0.45) 98%, rgba(76,76,76,0.46) 100%);
	background: -webkit-gradient(linear, left top, left bottom, from(rgba(250,242,250,0.04)), color-stop(3%, rgba(250,242,250,0.05)), color-stop(61%, rgba(28,28,28,0.3)), color-stop(84%, rgba(43,43,43,0.39)), color-stop(98%, rgba(20,19,20,0.45)), to(rgba(76,76,76,0.46)));
	background: linear-gradient(to bottom, rgba(250,242,250,0.04) 0%, rgba(250,242,250,0.05) 3%, rgba(28,28,28,0.3) 61%, rgba(43,43,43,0.39) 84%, rgba(20,19,20,0.45) 98%, rgba(76,76,76,0.46) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#faf2fa', endColorstr='#4c4c4c', GradientType=0 );
}

@media(min-width: 600px){
	.back{
		background: none;
	}

}
@media(max-width: 550px){
	.text{
		padding-top: 60%;
	}
	video{
		display: none;
	}
}


